 .footer {
     background: #13131a;
     text-align: center;
 }

 .footer .share {
     padding: 1rem 0;
 }

 .footer .share a {
     height: 5rem;
     width: 5rem;
     line-height: 5rem;
     font-size: 2rem;
     color: #fff;
     border: 0.2rem solid #ffc107;
     margin: 0.3rem;
     border-radius: 50%;
 }

 .footer .share a:hover {
     background-color: #ffc107;
 }

 .footer .links {
     display: flex;
     justify-content: center;
     flex-wrap: wrap;
     padding: 2rem 0;
     gap: 1rem;
 }

 .footer .links a {
     padding: 0.7rem 2rem;
     color: #fff;
     border: 0.2rem solid #ffc107;
     font-size: 2rem;
 }

 .footer .links a:hover {
     background: #ffc107;
 }

 .footer .credit {
     font-size: 2rem;
     color: #fff;
     font-weight: lighter;
     padding: 1.5rem;
 }

 .footer .credit span {
     color: #ffc107;
 }


 @media (max-width: 991px) {
     html {
         font-size: 55%;
     }

     .header {
         padding: 1.5rem 2rem;
     }

     section {
         padding: 2rem;
     }
 }

 @media (max-width: 768px) {
     #menu-btn {
         display: inline-block;
     }

     .header .navbar {
         position: absolute;
         top: 100%;
         right: -1000%;
         background: #000;
         width: 30rem;
         height: calc(100vh - 9.5rem);
         transition: 1.5s;
     }

     .header .navbar.active {
         right: 0;
         transition: 0.2s;
     }

     .header .navbar a {
         color: #fff;
         display: block;
         margin: 1.5rem;
         padding: 0.5rem;
         font-size: 2rem;
     }

     .header .search-form {
         width: 90%;
         right: 2rem;
     }

     .home {
         background-position: left;
         justify-content: center;
         text-align: center;
     }

     .home .content h3 {
         font-size: 4.5rem;
     }

     .home .content p {
         font-size: 1.5rem;
     }
 }

 @media (max-width: 650px) {
     html {
         font-size: 50%;
     }

     .menu .box-container {
         display: grid;
         grid-template-columns: auto auto;
     }

     .products .box-container {
         display: grid;
         grid-template-columns: auto;
     }

     .review .box-container {
         display: grid;
         grid-template-columns: auto;
     }

 }

 @media (max-width: 430px) {
     html {
         font-size: 50%;
     }

     .menu .box-container {
         display: grid;
         grid-template-columns: auto;
     }

     .products .box-container {
         display: grid;
         grid-template-columns: auto;
     }

     .review .box-container {
         display: grid;
         grid-template-columns: auto;
     }
 }